
<template>
  <div class="body" :class="ourClass">
    <h2>{{ title }}</h2>
    <div class="cont">
      <div class="col_l">
        <stage
        :ourClass="ourClass"
        :clientWidth="clientWidth"
          :src="`icon_1.png`"
          :number="'01'"
          :description="'Бесплатный выезд специалиста на объект'"
        />
        <stage
          :src="'../assets/img/icons/stage_1.png'"
          :number="'02'"
          :description="'Составление ТЗ и сметной документации'"
        />
        <stage
          :src="'../../assets/img/icons/stage_1.svg'"
          :number="'03'"
          :description="'Подписание договора и технической документации'"
        />
      </div>
      <div class="col_r">
        <stage
          :src="'../../assets/img/icons/stage_1.svg'"
          :number="'04'"
          :description="'Выезд бригады и транспортировка техники'"
        />
        <stage
          :src="'../../assets/img/icons/stage_1.svg'"
          :number="'05'"
          :description="'Выполнение работ'"
        />
        <stage
          :src="'../../assets/img/icons/stage_1.svg'"
          :number="'06'"
          :description="'Технический аудит и сдача объекта'"
        />
      </div>
    </div>
  </div>
</template>

<script>

import stage from "../stage.vue";
export default {
  data() {
    return {
        path: "http://localhost:8080/"
    };
  },
  name: "secstages",
  props: {
    title: String,
    clientWidth: Number
  },
  components: {
    stage,
  },
  computed: {
    ourClass() {
      let ourClass;
      if (this.clientWidth < 1200 && this.clientWidth > 1100) {
        ourClass = "medium";
      }
      if (this.clientWidth < 1100 && this.clientWidth > 900) {
        ourClass = "small";
      }
      if (this.clientWidth < 900 && this.clientWidth > 720) {
        ourClass = "tiny";
      }
      if (this.clientWidth < 720 && this.clientWidth > 670) {
        ourClass = "ultra_tiny";
      }
      if (this.clientWidth < 670) {
        ourClass = "mobile";
      }
      return ourClass;
    },
  },
  watch: {},
  methods: {},
};
</script>
