  mounted() {},
  created() {},
  components: {},
  watch: {},
  props: {
    nav: Object,
    admin: Boolean,
    username: String,
    clientWidth: Number,
    openMobNav: Boolean,
    ourClass: String,
  },
  computed: {},
  methods: {
    click(nav) {
      this.$emit("nav", nav);
      if (this.ourClass == "mobile") {
        this.$emit("openMobNav");
      }
    },
    dropdown(master, slave) {
      const a = master.getBoundingClientRect();
      const b = slave;
      const height = a.height;
      const width = a.width;
      const left = a.left + pageXOffset;
      b.style.top = height + "px";
      b.style.width = width + "px";
      b.style.left = left + "px";
    },
    async logOut() {
      await this.$axios.get("/user/log_out");
      window.location.reload();
    },
  },
};
